<template>
    <div ref="recorderRef">
        <h1>Recorder</h1>
        <a-button @click="startRecorder"></a-button>
        <a-button @click="stopRecorder"></a-button>
    </div>
</template>

<script lang="ts" setup>
import Recorder from 'recorder-core';
import 'recorder-core/src/engine/mp3.js';
import 'recorder-core/src/engine/mp3-engine.js';
import { ref } from 'vue';

const recorderRef = ref<HTMLDivElement | null>(null);

//(CSP)js
//Recorder+mp3await import("https://unpkg.com/recorder-core/recorder.mp3.min.js"); console.log("import ok")
//await import("https://unpkg.com/recorder-core/src/extensions/waveview.js"); console.log("import ok"); div=document.createElement("div");div.innerHTML='<div style="height:100px;width:300px;" class="recwave"></div>';document.body.prepend(div);

var rec, processTime, wave;
/**open**/
var recOpen = function (success) {
    //
    rec = Recorder({
        //
        type: 'mp3',
        sampleRate: 16000,
        bitRate: 16, //mp3hzkbpstypewavwav.js
        onProcess: function (
            buffers,
            powerLevel,
            bufferDuration,
            bufferSampleRate,
            newBufferIdx,
            asyncEnd,
        ) {
            //112bufferspcm(16LE)
            //extensions/sonic.jsonProcesstrue
            //Recorder.SampleDatabufferspcmmockDemo -> -ASR
            processTime = Date.now();

            //extensionswaveview.jswavesurfer.view.jsfrequency.histogram.view.js
            wave &&
                wave.input(
                    buffers[buffers.length - 1],
                    powerLevel,
                    bufferSampleRate,
                );
        },
    });

    rec.open(
        function () {
            //
            //rec.start() openopenstart

            //div
            if (Recorder.WaveView)
                wave = Recorder.WaveView({ elem: '.recwave' });
            success && success();
        },
        function (msg, isUserNotAllow) {
            //
            console.log(
                (isUserNotAllow ? 'UserNotAllow' : '') + ':' + msg,
            );
        },
    );
};

/****/
function recStart() {
    //startstop
    rec.start();
    var startTime = Date.now();
    //WDTonProcess
    var wdt = (rec.watchDogTimer = setInterval(function () {
        if (!rec || wdt != rec.watchDogTimer) {
            clearInterval(wdt);
            return;
        } //sync
        if (Date.now() < rec.wdtPauseT) return; //puaserec.wdtPauseT=Date.now()*2resumerec.wdtPauseT=Date.now()+10001
        if (Date.now() - (processTime || startTime) > 1500) {
            clearInterval(wdt);
            console.error(processTime ? '' : '');
            // ... 
        }
    }, 1000));
    rec.wdtPauseT = 0;
    processTime = 0;
}

/****/
function recStop() {
    rec.watchDogTimer = 0; //onProcess
    rec.stop(
        function (blob, duration) {
            //URLrevokeObjectURL
            //audio.srca.hrefa.click()adownload="xxx.mp3"
            var localUrl = (window.URL || webkitURL).createObjectURL(blob);
            console.log(blob, localUrl, ':' + duration + 'ms');
            rec.close(); //startclose
            rec = null;

            //blob

            /***  ***/
            var audio = document.createElement('audio');
            audio.style.display = 'block';
            recorderRef.value?.appendChild(audio);
            audio.controls = true;
            audio.src = localUrl;
            // audio.play();
        },
        function (msg) {
            console.log(':' + msg);
            rec.close(); //stop3close
            rec = null;
        },
    );
}

//3copy
// recOpen(function () {
//     recStart();
//     setTimeout(recStop, 3000);
// });

function startRecorder() {
    recOpen(function () {
        recStart();
    });
}

function stopRecorder() {
    recStop();
}

// onMounted(() => {
//     recOpen(function () {
//         recStart();
//         setTimeout(recStop, 3000);
//     });
// });
</script>
